import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { IoClose } from "react-icons/io5";
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import './style.css';
import logo from '../../img/icons/mini_logo.svg';
import { SlArrowRight } from "react-icons/sl";
import API_URL from '../../config'; //   API

const SuccessPage = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const phone = new URLSearchParams(location.search).get('phone');
  const [countdown, setCountdown] = useState(45);
  const [error, setError] = useState('');

  useEffect(() => {
    const timer = setInterval(() => {
      if (countdown > 0) {
        setCountdown(prevCountdown => prevCountdown - 1);
      }
    }, 1000);

    return () => clearInterval(timer);
  }, [countdown]);

  const resendCode = () => {
    setCountdown(45);
  };

  let accessToken; //   accessToken  

  const handleSubmit = async (values, { setSubmitting }) => {
    try {
      const formData = new URLSearchParams();
      formData.append('phone_number', phone);
      formData.append('verification_code', values.code);

      console.log(phone)
      console.log(values.code)

      const response = await fetch(`${API_URL}/verify-code/`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: formData.toString(),
      });

      console.log(response)

      if (response.status === 200) {
        const responseData = await response.json(); //     

        if (responseData.message) {
          console.log('  :', responseData);
        }

        //   refresh  access  Local Storage
        localStorage.setItem('refreshToken', responseData.refresh);
        localStorage.setItem('accessToken', responseData.access);
        accessToken = responseData.access; //   accessToken

        //    api/orders
        const ordersResponse = await fetch(`${API_URL}/orders/`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${accessToken}`
          },
          body: JSON.stringify({ value: "current" })
        });

        if (ordersResponse.status === 200) {
          //    api/orders ,    /main-page
          navigate('/main-page');
        } else {
          //       api/orders,  
          setError('   ');
        }

      } else {
        setError(' ');
      }
    } catch (error) {
      console.error('   :', error);
      setError(' ');
    } finally {
      setSubmitting(false);
    }
  };

  //   
  function isMobileDevice() {
      return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
  }
  const isMobile = isMobileDevice();

  return (
    <>
      <main className='login-container'>
        <div className='login-header'>
          <div className="login-id">
            <img src={logo} alt="Logo" />
            <p>ID</p>
          </div>
          <a href='/'>
            <div className="login-close">
              <IoClose />
            </div>
          </a>
        </div>
        <div className='login-main-block'>
          <h3> </h3>
          <p className='number'>    : +{phone}</p>
          <Formik
            initialValues={{ code: '' }}
            validationSchema={Yup.object().shape({
              code: Yup.string()
                .required(' ')
                .matches(/^[0-9]{6}$/, '    6 ')
            })}
            onSubmit={handleSubmit}
          >
            {({ isSubmitting }) => (
              <Form>
                <div className='form-inline'>
                  <div className='form-group'>
                    <label htmlFor="code"></label>
                      <Field name="code">
                        {({ field, form, meta }) => (
                          <input
                            {...field}
                            maxLength={6} //     5 
                            className={`floating-input ${meta.error && meta.touched ? 'input-error' : ''}`}
                            placeholder=' '
                          />
                        )}
                      </Field>
                    <ErrorMessage name="code" component="div" className="error-message" />
                  </div>
                  <button type="submit" className={`btn-login ${isSubmitting ? 'disabled' : ''}`}>
                      {isMobile ? '' : <SlArrowRight />}
                  </button>
                </div>
                {countdown > 0 ? (
                  <p className='countdown'>    {countdown} </p>
                ) : (
                  <button type="button" className='countdown-send' onClick={resendCode}> </button>
                )}

                {error && <div className="error-message">{error}</div>}
              </Form>
            )}
          </Formik>
        </div>
        <img className='bg-logo' src={logo} alt="Background Logo" />
      </main>
    </>
  );
};

export default SuccessPage;
