import React, { Component } from 'react';
import Header from '../../components/header/Header';

class Bonus extends Component {
  constructor(props) {
    super(props);
    this.inviteLinkRef = React.createRef(); //      
    this.state = {
      invitedFriends: [" ", " ", " "], //     
      copied: false, //       
    };
  }

  //    
  copyLink = () => {
    //   
    const referralLink = "https://x-solution.com/referral?user_id=123";

    //     
    navigator.clipboard.writeText(referralLink).then(() => {
      //    ,   copied  true
      this.setState({ copied: true });

      //     2 
      setTimeout(() => {
        this.setState({ copied: false });
      }, 2000);
    }).catch(error => {
      //   
      console.error(' :', error);
    });
  }

  render() {
    return (
        <>
          <div className='main-chat-div'>
            <Header />
            <div className='container header-padding'>
              <h2> </h2>
              <p>         .</p>
              {/*    */}
              <a href="#" ref={this.inviteLinkRef} onClick={this.copyLink}> </a>
              {/*     */}
              {this.state.copied && <p> !</p>}
              <h3> :</h3>
              <ul>
                {/*     */}
                {this.state.invitedFriends.map((friend, index) => (
                    <li key={index}>{friend}</li>
                ))}
              </ul>
            </div>
          </div>
        </>
    );
  }
}

export default Bonus;
