import React, { Component } from 'react';
import Header from '../../components/header/Header';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import FileInput from "../../components/FileInput/FileInput";
import API_URL from '../../config';
import customFetch from "../../utils/RefreshToken"; //   API


class NewOrder extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showFields: 'yes', //    ""
            login: '',
            password: '',
            additionalInfo: '',
            discipline_name: '',
            service_num: '',
            files: [],
        };
    }

    componentDidMount() {
        document.getElementById('yesRadio').checked = true;
    }

    handleRadioChange = (event) => {
        this.setState({showFields: event.target.value});
    }

    handleInputChange = (event) => {
        console.log(event.target.name, event.target.value); //   

        //   ,  
        if (event.target.type === 'file') {
            let files;
            if (event.target.files) {
                files = event.target.files;
            } else if (event.dataTransfer) {
                files = event.dataTransfer.files;
            }

            if (files) {
                //   , ,    
                this.setState({ selected_files: files });
            }
        } else {
            //      service_num  order_goal
            if (event.target.name === 'service_num') {
                this.setState({
                    [event.target.name]: event.target.value,
                    order_goal: event.target.value //  order_goal    
                });
            } else {
                this.setState({ [event.target.name]: event.target.value });
            }
        }
    }

    handleFileInputChange = (selectedFiles) => {
        this.setState({ files: selectedFiles });
    };


    handleSubmit = (event) => {
        event.preventDefault();

        let requestBody;

        if (this.state.showFields === 'yes') {
            requestBody = JSON.stringify({
                order_goal: this.state.service_num,
                lk_log: this.state.login,
                lk_pas: this.state.password,
                dop_info: this.state.additionalInfo || 'your info'
            });
        } else if (this.state.showFields === 'no') {
            const formData = new FormData(); //   FormData   

            //      FormData
            for (const file of this.state.files) {
                formData.append('files', file);
            }

            //      FormData
            formData.append('order_goal', this.state.service_num);
            formData.append('discipline_name', this.state.discipline_name);
            formData.append('deadline', this.state.deadline);
            formData.append('dop_info', this.state.additionalInfo || 'your info');

            requestBody = formData;
        }

        const accessToken = localStorage.getItem('accessToken'); //  ,   
        const headers = {
            'Authorization': `Bearer ${accessToken}`,
        };

        const requestOptions = {
            method: 'POST',
            headers: headers,
        };

        if (this.state.showFields === 'no') {
            //   FormData,   Content-Type  'multipart/form-data'
            //     FormData   body
            requestOptions.body = requestBody;
            console.log("showFields:", this.state.showFields);
            console.log("requestBody:", requestBody);
            console.log("fetching with requestOptions:", requestOptions);
        } else {
            //   JSON,   Content-Type  'application/json'
            //    JSON   body
            headers['Content-Type'] = 'application/json';
            requestOptions.body = requestBody;
            console.log("showFields:", this.state.showFields);
            console.log("requestBody:", requestBody);
            console.log("fetching with requestOptions:", requestOptions);
        }

        customFetch(`${API_URL}/order-request/`, requestOptions)
            .then(response => {
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                return response.json();
            })
            .then(data => {
                console.log('Response from server:', data);
                // ,      order_id
                if (data && data.order_id) {
                    //  URL   
                    const orderId = data.order_id;
                    const redirectUrl = `/new-order-page/${orderId}`;

                    //      
                    window.location.href = redirectUrl;
                } else {
                    //   order_id    ,
                    //        
                    console.error('Order ID is missing in the server response');
                }
            })
            .catch(error => {
                console.error('There was an error with the fetch operation:', error);
                //      
            });

    };

    render() {
            return (
                <>
                    <Header/>
                    <div className='container header-padding text-center'>
                        <h2 className='p-3 mt-4'>  </h2>
                        <div className='form-block'>
                            <Row className="">
                                {/*      */}
                                <form onSubmit={this.handleSubmit}>
                                    {/*   */}
                                    <Col md={{ span: 6, offset: 3 }}>
                                        <FloatingLabel
                                            controlId="floatingSelectGrid1"
                                            label=" "
                                            className="mb-3"
                                        >
                                            <Form.Select itemID="floatingSelectGrid1" className="custom-input-main" aria-label="Floating label select example">
                                                <option>  </option>
                                                <option value="1"> </option>
                                                <option value="2">   </option>
                                                <option value="3">   </option>
                                            </Form.Select>
                                        </FloatingLabel>
                                    </Col>

                                    <Col md={{ span: 6, offset: 3 }}>
                                        <FloatingLabel
                                            controlId="floatingSelectGrid2"
                                            label=""
                                            className="mb-3"
                                        >
                                            <Form.Select
                                                className="custom-input-main"
                                                itemID="floatingSelectGrid2"
                                                aria-label="Floating label select example"
                                                onChange={this.handleInputChange}
                                                name='service_num'
                                            >
                                                <option></option>

                                                <option value="  ">  </option>
                                                <option value=" "> </option>
                                                <option value="  ">  </option>
                                                <option value=""></option>
                                                <option value=" "> </option>
                                                <option value=" "> </option>

                                                <option value=" "> </option>
                                                <option value=" "> </option>
                                                <option value=""></option>
                                                <option value=" "> </option>
                                                <option value=" "> </option>
                                                <option value=" "> </option>
                                                <option value=""></option>
                                                <option value=" "> </option>
                                                <option value=" "> </option>
                                                <option value=""></option>
                                                <option value=""></option>
                                                <option value=" "> </option>
                                                <option value=""></option>

                                                <option value=""></option>
                                                <option value="-">-</option>
                                            </Form.Select>
                                        </FloatingLabel>
                                    </Col>

                                    <Col md={{span: 6, offset: 3}}>
                                    <span className="main-radio-span mb-3">
                                      <label className="main-radio-span-one">
                                        <input id="yesRadio" className="custom-input" type="radio" name="showFields" value="yes"
                                               onChange={this.handleRadioChange}/>
                                        <span className="input-span-text"></span>
                                      </label>
                                      <label className="main-radio-span-two">
                                        <input id="noRadio" className="custom-input" type="radio" name="showFields" value="no"
                                               onChange={this.handleRadioChange}/>
                                        <span className="input-span-text"></span>
                                      </label>
                                    </span>
                                    </Col>

                                    {this.state.showFields === 'yes' && (
                                        <>
                                            <Col md={{span: 6, offset: 3}}>
                                                <FloatingLabel
                                                    controlId="floatingInput1"
                                                    label="   "
                                                    className="mb-3"
                                                >
                                                    <Form.Control
                                                        className="custom-input-main"
                                                        itemID="floatingInput1"
                                                        type="text"
                                                        placeholder=""
                                                        onChange={this.handleInputChange}
                                                        name="login"
                                                    />
                                                </FloatingLabel>
                                            </Col>

                                            <Col md={{span: 6, offset: 3}}>
                                                <FloatingLabel
                                                    controlId="floatingInput2"
                                                    label="   "
                                                    className="mb-3"
                                                >
                                                    <Form.Control
                                                        className="custom-input-main"
                                                        itemID="floatingInput2"
                                                        type="text"
                                                        placeholder=""
                                                        onChange={this.handleInputChange}
                                                        name="password"
                                                    />
                                                </FloatingLabel>
                                            </Col>
                                        </>
                                    )}

                                    {this.state.showFields === 'no' && (
                                        <>
                                            <Col md={{ span: 6, offset: 3 }}>
                                                <p>  :</p>
                                                <FloatingLabel
                                                    controlId="floatingInput3"
                                                    label=""
                                                    className="mb-3 custom-input-main"
                                                >
                                                    <Form.Control
                                                        className="custom-input-main"
                                                        itemID="floatingInput3"
                                                        type="text"
                                                        placeholder=""
                                                        onChange={this.handleInputChange}
                                                        name="discipline_name"
                                                    />
                                                </FloatingLabel>
                                            </Col>

                                            <Col md={{span: 6, offset: 3}}>
                                                <FloatingLabel
                                                    controlId="floatingInput4"
                                                    label=" "
                                                    className="mb-3"
                                                >
                                                    <Form.Control
                                                        className="custom-input-main"
                                                        itemID="floatingInput4"
                                                        type="datetime-local"
                                                        placeholder="  /  "
                                                        onChange={this.handleInputChange}
                                                        name="deadline"
                                                    />
                                                </FloatingLabel>
                                            </Col>

                                            <Col className="mb-3" md={{span: 6, offset: 3}}>
                                                <FileInput
                                                    onChange={this.handleFileInputChange}
                                                />
                                            </Col>
                                        </>
                                    )}

                                    <Col md={{span: 6, offset: 3}}>
                                        <FloatingLabel controlId="floatingTextarea2" label="">
                                            <Form.Control
                                                className="custom-input-main mb-3"
                                                as="textarea"
                                                placeholder="Leave a comment here"
                                                style={{height: '100px'}}
                                                onChange={this.handleInputChange}
                                                name="additionalInfo"
                                            />
                                        </FloatingLabel>
                                    </Col>
                                    <Col md={{span: 6, offset: 3}}>
                                        <Button type="submit" className='w-100' variant="warning"></Button>
                                    </Col>
                                    
                                </form>
                            </Row>
                        </div>
                    </div>
                </>
            );
        }

    }

export default NewOrder;
