import React, { Component } from 'react';
import Header from '../../components/header/Header';
import { GoChecklist } from "react-icons/go";
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import { NavLink } from 'react-router-dom';

import API_URL from '../../config';
import customFetch from "../../utils/RefreshToken"; //   API

class NewOrders extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orders: []
    };
  }

  async componentDidMount() {
    try {
      const accessToken = localStorage.getItem('accessToken'); //  
      //      
      const newOrdersResponse = await customFetch(`${API_URL}/orders/`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${accessToken}`
        },
        body: JSON.stringify({ value: "new" })
      });
      if (!newOrdersResponse.ok) {
        throw new Error(`Failed to fetch new orders: ${newOrdersResponse.statusText}`);
      }
      const newOrdersData = await newOrdersResponse.json();

      //      
      const oldOrdersResponse = await customFetch(`${API_URL}/orders/`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${accessToken}`
        },
        body: JSON.stringify({ value: "old" }) //     
      });
      if (!oldOrdersResponse.ok) {
        throw new Error(`Failed to fetch old orders: ${oldOrdersResponse.statusText}`);
      }
      const oldOrdersData = await oldOrdersResponse.json();

      //      
      this.setState({
        orders: newOrdersData.orders,
        oldOrders: oldOrdersData.orders
      });
    } catch (error) {
      console.error('Error fetching orders:', error);
    }
  }

  render() {
    const { orders, oldOrders } = this.state;

    return (
        <>
          <Header />
          <div className='container header-padding'>
            <h2 className='mt-3 mb-3'> </h2>
            <Tabs
                defaultActiveKey="home"
                id="uncontrolled-tab-example"
                className="mb-4"
            >
              <Tab eventKey="home" title="">
                {orders.length === 0 ? (
                    <>
                      <h3 className='mb-4'>     </h3>

                      <div className='sale-block'>
                        <div className='sale-block-one'>
                          <div>
                            <h5>  30%</h5>
                            <p>      ,       30%   </p>
                          </div>
                          <GoChecklist />
                        </div>
                        <div className='sale-block-two'>
                          <NavLink to='/new-order'>
                             
                          </NavLink>
                        </div>
                      </div>
                    </>
                ) : (
                    <ul className='order-list'>
                      {orders.map(order => (
                          <li key={order.order_id}>
                            <div>
                              <GoChecklist />
                              <p>{order.order_id}</p>
                            </div>
                            <p>{order.status}</p>
                            <p>{order.date}</p>
                            <NavLink to={`/new-order-page/${order.order_id}`}>
                              
                            </NavLink>
                          </li>
                      ))}
                    </ul>
                )}
              </Tab>
              <Tab eventKey="profile" title="">
                {oldOrders && oldOrders.length === 0 ? (
                    <h3 className='mb-4'>     </h3>
                ) : (
                    <ul className='order-list'>
                      {oldOrders && oldOrders.map(order => (
                          <li key={order.order_id}>
                            <div>
                              <GoChecklist />
                              <p>{order.order_id}</p>
                            </div>
                            <p>{order.status}</p>
                            <p>{order.date}</p>
                            <NavLink to={`/archive-order-page/${order.order_id}`}>
                              
                            </NavLink>
                          </li>
                      ))}
                    </ul>
                )}
              </Tab>
            </Tabs>
          </div>
        </>
    );
  }
}

export default NewOrders;